/* General Styles */
body {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    background-color: #f4f8ef; /* Soft, fresh greenish-white */
    color: #2e4a29; /* Deep, muted green */
}

/* Header styles */
header {
    text-align: center;
    margin-bottom: 20px;
}

h1 {
    font-size: 2.5em;
    color: #216e39; /* Deep emerald green */
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
    margin: 0;
}

/* March Theme header text with gold accents */
h2 {
    color: #cdaa50; /* Rich gold */
    font-style: italic;
    margin: 0;
}

/* Profile image */
.profile-img {
    width: 100%;
    max-width: 400px;
    height: auto;
    object-fit: cover;
    border: 8px solid #216e39; /* Emerald green border */
    border-radius: 50%;
    box-shadow: 0px 0px 25px rgba(34, 139, 34, 0.4); /* Green glow effect */
    transition: transform 0.3s ease-in-out;
    margin-top: 15px;
}

.profile-img:hover {
    transform: scale(1.05);
}

/* Container styling */
.container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

/* Section styling */
section {
    background: #ffffff;
    border-radius: 12px;
    padding: 20px;
    margin: 20px 0;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
    border: 1px solid #c5d1a4; /* Soft sage green border */
    backdrop-filter: blur(5px);
}

/* March Theme - Shamrocks around list items */
ul {
    list-style-type: none;
    padding: 0;
}

ul li::before {
    content: 'üçÄ'; /* Shamrock icon for a March touch */
    margin-right: 10px;
    color: #216e39; /* Emerald green */
}

/* March-themed horizontal rule */
hr {
    margin: 20px 0;
    border: 1px solid #cdaa50; /* Gold accent */
}

/* Footer styling */
footer {
    text-align: center;
    margin-top: 20px;
    font-size: 0.9em;
    color: #216e39;
    background-color: #c5d1a4; /* Light sage green */
    padding: 15px 0;
    border-radius: 8px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
}

/* March accent for footer */
footer p {
    color: #cdaa50; /* Gold for festive elegance */
}
